
import './App.css';
import http from './utils/http.js'
import { useEffect, useState } from 'react'
import { Image } from 'antd';

function App() {
  const [homeInfo,sethomeInfo] = useState([]);
  useEffect(() => {
     http.get('/api/v3/tag/list', {pid:0, apiver:2, plat:0}).then(res=>{
      const { data:{ info } } = res;
      let arr =[];
      info.map(item=>{
        arr = arr.concat(item.children)
      })
      sethomeInfo(arr);
    })
  },[]);

  // 随机颜色
  const randomColor=()=>{
    //井号放在开头拼接
    let colorStr = "#";
    let str = "ABCDEF123456789";
    for(let i = 0; i < 6; i++){
      colorStr += str[Math.floor(Math.random() * str.length)]
    }
    return colorStr;
  }

  return (
    <div className="App">
      <div className="sortTitle">
        曲目分类
      </div>
      <div className="home-icons">
        {
          homeInfo.map(item=>{
            if(item.jump_url){
              return (
                <div className="home-icon" key={item.id}>
                  <a href={item.jump_url}>
                    <div className="imgs">
                      <Image
                          width={80}
                          height={80}
                          preview={false}
                          style={item.bannerurl?{}:{background: randomColor()}}
                          src={ item.bannerurl? `${item.bannerurl.substring(0,item.bannerurl.indexOf('{'))}${item.bannerurl.substring(item.bannerurl.lastIndexOf('}')+2, item.bannerurl.length)}`:
                          `${item.icon.substring(0,item.icon.indexOf('{'))}${item.icon.substring(item.icon.lastIndexOf('}')+2, item.icon.length)}`}
                          fallback="data:image/png;base64,"
                        />
                    </div>
                      <div className="home-icon-font">
                        {item.name}
                      </div>
                  </a>
                </div>
              )
            }
          })
        }
      </div>
    </div>
  );
}

export default App;
